<template>
  <v-app>
    <v-app-bar app color="indigo darken-4" dark clipped-left class="d-flex justify-center">
      <router-link to="/">
        <v-btn text>
          <div class="d-flex align-center">
            <v-img
              alt="Atakan's Picture"
              class="shrink mr-2"
              contain
              src="./assets/pp2.jpeg"
              transition="scale-transition"
              width="40"
            />

            <v-toolbar-title class="text-center">Atakan GÜNEY</v-toolbar-title>
          </div>
        </v-btn>
      </router-link>

      <v-spacer></v-spacer>

      <v-app-bar-nav-icon @click="handleClick" class="d-md-none"></v-app-bar-nav-icon>
    </v-app-bar>

    <v-navigation-drawer
      clipped
      app
      absolute
      v-model="drawer"
      :temporary="isMobile"
      desible-resize-watcher="true"
    >
      <v-list-item v-for="(nav,index) in navs" :key="index" class="text-center">
        <v-list-item-title>
          <router-link :to="nav.path">
            <v-btn width="100%">{{ nav.name }}</v-btn>
          </router-link>
        </v-list-item-title>
      </v-list-item>
    </v-navigation-drawer>

    <v-content>
      <router-view />
    </v-content>

    <v-footer absolute padless app>
      <v-card flat tile width="100%" class="text-center" color="indigo darken-4">
        <v-card-text>
          <v-btn
            class="mx-2"
            color="indigo"
            v-for="(item, index) in socialMedia"
            :key="index"
            :href="item.link"
          >
            <font-awesome-icon :icon="item.icon" color="white" />
          </v-btn>
        </v-card-text>
      </v-card>
    </v-footer>
  </v-app>
</template>

<script>
export default {
  name: 'App',

  data: () => ({
    navs: [
      {
        name: 'About',
        path: '/about',
      },
      {
        name: 'Resume',
        path: '/resume',
      },
      {
        name: 'Research',
        path: '/research',
      },
    ],
    socialMedia: [
      {
        link: 'https://www.facebook.com/atkn1907',
        icon: ['fab', 'facebook'],
      },
      {
        link: 'https://twitter.com/atakanguney2',
        icon: ['fab', 'twitter'],
      },
      {
        link: 'https://medium.com/@atakanguney94',
        icon: ['fab', 'medium'],
      },
      {
        link: 'https://www.linkedin.com/in/atakan-g%C3%BCney-987ab1122/',
        icon: ['fab', 'linkedin'],
      },
    ],
    drawerDummy: false,
    isMobile: window.matchMedia('(max-width: 960px)').matches,
  }),

  computed: {
    drawer: {
      get() {
        return this.drawerDummy || !this.isMobile;
      },

      set(newValue) {
        this.drawerDummy = newValue;
      },
    },
  },

  methods: {
    handleClick() {
      this.drawerDummy = !this.drawerDummy;
    },

    onResize() {
      this.isMobile = window.matchMedia('(max-width: 960px)').matches;
    },
  },

  mounted() {
    window.addEventListener('resize', this.onResize);
  },

  destroyed() {
    window.removeEventListener('resize', this.onResize);
  },
};
</script>
